import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, NaturezaReceita } from '../../../../models/dtos/ger/naturezaReceita';
import { NATUREZA_RECEITA_CST_ENUM_VALUES } from '../../../../models/enumerated/ger/naturezaReceitaCSTEnum';

interface NaturezaReceitaEditViewProps extends EditViewProps<NaturezaReceita> {}

const NaturezaReceitaEditView: FC<NaturezaReceitaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<NaturezaReceita>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção da Natureza de Receita">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 3 } }}
              id="codigo"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Código"
              placeholder="Informe um Código"
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              id="cst"
              label="CST"
              isPrependLabel={true}
              placeholder="Selecione um CST"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: NATUREZA_RECEITA_CST_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 500, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição"
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default NaturezaReceitaEditView;
